import React from "react";
import { withStyles } from "@material-ui/core/styles";
import classNames from "classnames";
import compose from "../../../compose";

const styles = (theme) => ({
    icon: {
        height: "20px",
        width: "auto",
    },
});

function Icon(props) {
    const { classes } = props;
    return (
        <>
            <svg
                xmlns="http://www.w3.org/2000/svg"
                width="100%"
                height="100%"
                fill="none"
                viewBox="0 0 32 32"
                className={classNames(classes.icon, props.className)}
                style={{ height: `${props.size}px` }}>
                <path stroke="currentColor" strokeWidth="1.5" d="M19.5 10l3.5 3.5v7L19.5 24h-7L9 20.5v-7l3.5-3.5M16 8v8"></path>
            </svg>
        </>
    );
}

export default compose(withStyles(styles))(Icon);
