import React from "react";
import { withStyles } from "@material-ui/core/styles";
import classNames from "classnames";
import compose from "../../../compose";

const styles = (theme) => ({
  icon: {
    height: "20px",
    width: "auto",
  },
});

function BackpackIcon(props) {
  const { classes } = props;

  return (
    <>
      <svg
        width="100%"
        height="100%"
        viewBox="0 0 32 32"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
        className={classNames(classes.icon, props.className)}
        style={{ height: `${props.size}px` }}
        onClick={() => props.onClick && props.onClick()}
      >
        <path
          d="M13 6H11L8.5 8V11L10 13M13 6V16M13 6H19M19 6H21L23.5 8V11L22 13M19 6V16M10 13H11H21H22M10 13V18M22 13V18M22 18H24L25 19V22L24 23H22M22 18V23M22 23V24L20 26H12L10 24V23M10 23H8L7 22V19L8 18H10M10 23V18"
          stroke="currentColor"
          strokeWidth="1.5"
        />
      </svg>
    </>
  );
}

export default compose(withStyles(styles))(BackpackIcon);
