import React, {useState, useEffect} from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { withLocalization } from '../shared/localization';
import withStore from '../../consumers/withStore';
import withDoby from '../../consumers/withDoby';
import getSharedStyles from '../../style';
import compose from '../../compose';
import classNames from 'classnames';
//import Button from './form/Button';
import moment from "moment";

import StopwatchIcon from '../dobyComponents/icons/Stopwatch';
import MountainIcon from '../dobyComponents/icons/Mountain';
import ArrowRight from '../dobyComponents/icons/ArrowRight';
import FlagIcon from '../dobyComponents/icons/Flag';

const styles = theme => ({
    ...getSharedStyles(theme),

    card: {
        width: '100%',        
        height: '270px',     
        position: 'relative',
        overflow: 'hidden',         
    },   
    challengeImgHolder: {
        //height: '270px',
        position: 'absolute',
        width: '100%',
        height: '100%',
        top: '0',
        left: '0',
        '&:after': {
            content: "''",
            display: 'block',
            position: 'absolute',
            bottom: '0',
            left: '0',
            width: '100%',
            height: '100%',
            background: 'linear-gradient(180deg, rgba(0, 20, 31, 0.32) 0%, rgba(2, 16, 25, 0.55) 56.58%, rgba(3, 15, 23, 0.6) 100%)',
        }
    },
    cardContent: {
        zIndex: 10,
        position: 'relative',
    },
    challengeImg: {
        position: 'center',
        left: '0',
        right: '0',
        width: '100%',
        height: '100%',
        objectFit: 'cover',
    },

    logoHolder: {
        position: 'absolute',
        top: '20px',
        left: '20px'
    },
    challengeLogo: {
        width: '90px',
        height: 'auto'
    },   
    challengeBtn: {
        width: '50px',
        height: '50px',
        background: theme.palette.primary.main,
        position: 'absolute',
        bottom: '0',
        right: '0',
    },       
});

function ChallengeCard(props) {
    const [image, setImage] = useState(null);
    const [logo, setLogo] = useState(null);
    const [map, setMap] = useState(null)

    useEffect(() => {       
        const fetchData = async() => {
            if(props.image) {
                const data = await props.doby.getFile(props.image);
                const reader = new FileReader();
                reader.onloadend = () => {
                    setImage(reader.result);
                }
                reader.readAsDataURL(data);
            }
            if(props.logo) {
                const data = await props.doby.getFile(props.logo);
                const reader = new FileReader();
                reader.onloadend = () => {
                    setLogo(reader.result);
                }
                reader.readAsDataURL(data);
            }           
        }  
        
        fetchData();
    }, [props.doby, props.image, props.logo]);

    const { classes, className, translate, finishedCount } = props;

    const summitsCount = props.summits && props.summits.length
    //const startDate = moment(props.start).format('DD.MM.YYYY');      
    
    let months = moment(props.end).diff(moment(props.start), 'months', true)
    months = Math.abs(months);
    months = Math.ceil(months)

    return (
        <div className={classNames(classes.card, className)} onClick={props.onClick && props.onClick}>
            <div className={classNames(classes.challengeImgHolder)}>
                <img src={image} className={classNames(classes.challengeImg)} alt=""/>
            </div>
            <div className={classNames(classes.dFlex, classes.flexCol, classes.w100, classes.h100, classes.justifyContentBetween, classes.cardContent)}>
                <div className={classNames(classes.logoHolder, classes.w100,)}>
                    <img src={logo} className={classNames(classes.challengeLogo)} alt=""/>
                </div>
                <div className={classNames(classes.p6, classes.dFlex, classes.flexCol, classes.h100, classes.justifyContentEnd)}>                                        
                    {/* <div className={classNames(classes.dFlex, classes.alignItemsCenter)}>
                        <MountainIcon size={25} className={classes.textWhite}/>
                        <div className={classNames(classes.textH4, classes.fontMedium, classes.textWhite, classes.textMono, classes.ml3)}>
                            {finishedCount} / {summitsCount} {finishedCount === 1 ? translate('challenge.summit') : finishedCount < 5 ? translate('challenge.summits') : translate('challenge.summit_gen')}
                        </div>
                    </div>
                    <div className={classNames(classes.dFlex, classes.alignItemsCenter, classes.mt2)}>
                        <StopwatchIcon size={25} className={classes.textWhite}/>
                        <div className={classNames(classes.textH4, classes.fontMedium, classes.textWhite, classes.textMono, classes.ml3)}>
                            {months} {months === 1 ? translate('challenge.month') : months < 5 ? translate('challenge.months') : translate('challenge.month_gen')}
                        </div>
                    </div>                    
                    <div className={classNames(classes.dFlex, classes.alignItemsCenter, classes.mt2)}>
                        <FlagIcon size={25} className={classes.textWhite}/>
                        <div className={classNames(classes.textH4, classes.fontMedium, classes.textWhite, classes.textMono, classes.ml3)}>
                            {moment(props.start).format('DD.MM')} - {moment(props.end).format('DD.MM')}
                        </div>
                    </div> */}
                    <div className={classNames(classes.dFlex, classes.alignItemsCenter, classes.mt2)}>
                        <FlagIcon size={25} className={classes.textWhite}/>
                        <div className={classNames(classes.textH4, classes.fontMedium, classes.textWhite, classes.textMono, classes.ml3)}>
                        Zakpen akajca vzva
                        </div>
                    </div>
                    <div className={classNames(classes.dFlex, classes.flexCol, classes.w100, classes.justifyContentStart, classes.mt7)}>
                        <div className={classNames(classes.textGray100, classes.textS, classes.fontMedium)}>
                            Od {moment(props.end).format('DD.M.YYYY')}
                        </div>                  
                    </div>
                </div>                
            </div>

            <div className={classNames(classes.challengeBtn, classes.dFlex, classes.alignItemsCenter, classes.justifyContentCenter)}>
                <ArrowRight size={28} className={classNames(classes.textSecondary)}/>
            </div>
        </div>
    );
}

ChallengeCard.propTypes = {
    className: PropTypes.string.isRequired,
    onClick: PropTypes.func.isRequired,
    title: PropTypes.string.isRequired,
    start: PropTypes.string, 
    difficulty: PropTypes.number,  
};

export default compose(
    withStyles(styles),
    withLocalization,
    withStore,
    withDoby
)(ChallengeCard);