import React from "react";
import { withStyles } from "@material-ui/core/styles";
import compose from "../../../compose";

import classNames from "classnames";
import getSharedStyles from "../../../style";

const styles = (theme) => ({
  ...getSharedStyles(theme),
  socialConnectCard: {
    background: "white",
  },
  socialIcon: {
    width: "30px",
    height: "30px",
  },
});

function FacebookConnection(props) {
  const { classes } = props;

  const handleClick = () => {
    const redirect = process.env.REACT_APP_HOMEPAGE;
    const url = `https://www.facebook.com/v9.0/dialog/oauth?scope=email,user_link&client_id=${props.config.id}&redirect_uri=${redirect}/?provider=5&state=1`;
    window.location.replace(url);
  };

  if (!props.config) {
    return null;
  }

  return (
    <>
      <div
        className={classNames(
          classes.p3,
          classes.socialConnectCard,
          classes.roundedMd,
          classes.dBlock
        )}
        onClick={handleClick}
      >
        <div
          className={classNames(
            classes.dFlex,
            classes.w100,
            classes.alignItemsCenter
          )}
        >
          <img
            src={"./img/fb-logo.png"}
            className={classNames(classes.socialIcon)}
            alt=""
          />
          <div className={classNames(classes.flexCol, classes.ml3)}>
            <div className={classNames()}>Prida a prepoji Facebook</div>
            {/* <div className={classNames(classes.textM, classes.text300)}>
                            Njs priateov a prida link na profil
                        </div> */}
          </div>
        </div>
      </div>
    </>
  );
}

export default compose(withStyles(styles))(FacebookConnection);
