import React from "react";
import { withRouter } from "react-router";
import withStore from "../../consumers/withStore";
import withDoby from "../../consumers/withDoby";
import withUser from "../../consumers/withUser";
import withUtils from "../shared/utils/withUtils";

//import PropTypes from 'prop-types';
import { withStyles } from "@material-ui/core/styles";
import { withLocalization } from "../shared/localization";

import classNames from "classnames";
import compose from "../../compose";
import getSharedStyles from "../../style";

import Select from "../dobyComponents/form/SelectField";
import Button from "../dobyComponents/form/Button";
import TextField from "../dobyComponents/form/TextField";
import Switch from "../dobyComponents/form/Switch";

import CircularProgress from "@material-ui/core/CircularProgress";
import Drawer from "@material-ui/core/Drawer";

import config from "../../config";

const styles = (theme) => ({
  ...getSharedStyles(theme),
  image: {
    width: "100%",
  },
});

class Push extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      challenges: [],
      summits: [],
      selectedChallengeRef: null,
      selectedSummitRef: null,
      summitDesc: "",
      summitIsPrivate: false,
      picture: null,
      uploading: false,
      modalOpened: false,
    };
  }

  componentDidMount() {
    this.getData();
  }

  getData = async () => {
    const { user } = this.props.user;
    const { challenges } = await this.props.doby.getUser(user._id, true);
    this.setState({ challenges });
  };

  resizeAndUpload = async (fileData, size) => {
    let data = await this.props.utils.resize(fileData, size);
    data = data.split(",").pop();

    let pictureBlob = this.props.utils.base64toBlob(data, "data/image");
    return await this.props.store.postFile(pictureBlob);
  };

  handleChangePicture = (event) => {
    event.preventDefault();

    const reader = new FileReader();
    reader.onloadend = async () => this.setState({ picture: reader.result });
    reader.readAsDataURL(event.target.files[0]);

    // zabezpecuje volanie onChange aj pri uploadovani rovnakeho suboru
    // -> po ulozeni formularu nezavolalo onChange opat, v pripade nahrania
    // -> rovnakeho suboru ako predtym
    event.target.value = "";
  };

  send = async () => {
    this.setState({ uploading: true });

    try {
      const pictureUploaded = await this.resizeAndUpload(
        this.state.picture,
        config.imageSize
      );
      const pictureThumbnailUploaded = await this.resizeAndUpload(
        this.state.picture,
        config.thumbnailSize
      );

      const { user } = this.props.user;
      await this.props.doby.addClimb({
        userRef: {
          id: user._id,
        },
        description: this.state.summitDesc,
        imageId: pictureUploaded._id,
        thumbnailId: pictureThumbnailUploaded._id,
        private: this.state.summitIsPrivate,
        summitRef: {
          id: this.state.selectedSummitRef,
        },
        challengeRef: {
          id: this.state.selectedChallengeRef,
        },
      });

      this.setState({ modalOpened: true });
    } catch (e) {
      console.error(e);
    }

    this.setState({ uploading: false });
  };

  onChangeChallenge = async (field, selectedChallengeRef) => {
    const challenge = this.state.challenges.find(
      (x) => x._id === selectedChallengeRef
    );
    if (challenge && this.state.selectedChallengeRef !== selectedChallengeRef) {
      const summitFilter = [
        {
          field: "_id",
          op: "in",
          value: challenge.summitsRef.map((x) => x.id),
        },
      ];

      this.setState({ selectedChallengeRef });

      const summits = await this.props.doby.getSummits(summitFilter);
      this.setState({ summits: summits.data });
    }
  };

  onChangeSummit = (field, selectedSummitRef) =>
    this.setState({ selectedSummitRef });

  closeModal = () => this.setState({ modalOpened: false });

  render() {
    const { classes } = this.props;
    return (
      <div className={classNames(classes.container, classes.mt3)}>
        <h2>{this.props.translate("summit.add")}</h2>
        {this.state.uploading && <CircularProgress />}
        <label for="image-upload">
          <div>
            <img
              className={classNames(classes.image, classes.mb3)}
              alt=""
              src={
                this.state.picture
                  ? this.state.picture
                  : "https://www.google.com/logos/doodles/2021/st-patricks-day-2021-6753651837108887.2-l.png"
              }
            />
            <input
              hidden
              type="file"
              accept="image/jpeg"
              onChange={this.handleChangePicture}
              id="image-upload"
            />
          </div>
        </label>

        <h3>{this.props.translate("summit.image_title")}</h3>
        <p>{this.props.translate("summit.image_desc")}</p>

        <div>
          <Select
            value={
              this.state.selectedChallengeRef
                ? this.state.selectedChallengeRef.toString()
                : ""
            }
            field={"challenge"}
            onChange={this.onChangeChallenge}
            data={this.state.challenges}
            textField={"name"}
            fullWidth
            required
          />

          <Select
            value={
              this.state.selectedSummitRef
                ? this.state.selectedSummitRef.toString()
                : ""
            }
            field={"summit"}
            onChange={this.onChangeSummit}
            data={this.state.summits}
            textField={"name"}
            fullWidth
            required
          />

          <TextField
            value={this.state.summitDesc}
            field={"summitDesc"}
            onChange={(field, summitDesc) => {
              this.setState({ summitDesc });
            }}
            fullWidth
            multiline
          />

          <div
            className={classNames(
              classes.dFlex,
              classes.w100,
              classes.justifyContentBetween,
              classes.alignItemsCenter
            )}
          >
            <h3 className={classNames(classes.mt4)}>
              {this.props.translate("summit.private")}
            </h3>
            <Switch
              field={"private"}
              value={this.state.summitIsPrivate}
              onChange={(field, summitIsPrivate) => {
                this.setState({ summitIsPrivate });
              }}
            />
          </div>

          <p>{this.props.translate("summit.private_desc")}</p>

          <Button
            className={classNames(classes.mt5, classes.mb3)}
            onClick={this.send}
            fullwidth
            disabled={
              !this.state.selectedChallengeRef ||
              !this.state.selectedSummitRef ||
              !this.state.picture ||
              !this.state.summitDesc.length
            }
          >
            {this.props.translate("summit.send")}
          </Button>
        </div>

        <Drawer
          anchor="bottom"
          open={this.state.modalOpened}
          onClose={this.closeModal}
        >
          <div className={classNames(classes.container, classes.mt5)}>
            <h2>{this.props.translate("summit.sent")}</h2>
          </div>
        </Drawer>
      </div>
    );
  }
}

export default compose(
  withRouter,
  withStore,
  withStyles(styles),
  withDoby,
  withUser,
  withLocalization,
  withUtils
)(Push);
