import React, { useState}  from "react";
import { withStyles } from "@material-ui/core/styles";
import getSharedStyles from "../../../style";
import classNames from "classnames";
import { withLocalization } from "../../shared/localization";
import { withRouter } from "react-router";
import compose from "../../../compose";
import MenuIcon from "../icons/HamburgerMenu";
import BackpackIcon from '../icons/Backpack'
import NotificationIcon from '../icons/Notification'
import ProfileIcon from '../icons/Profile'
import config from "../../../config";
import permissions from "../../../permissions";
import { Transition } from 'react-transition-group';

import HomeIcon from "../icons/Home";
import HomeFilledIcon from "../icons/HomeFilled";
import UserIcon from "../icons/User";
import UserFilledIcon from "../icons/UserFilled";
import MedalIcon from "../icons/Medal";
import MedalFilledIcon from "../icons/MedalFilled";
import MoreDotsIcon from "../icons/MoreDots";
import ShirtIcon from "../icons/TShirt";
import PlusIcon from "../icons/Plus";
import LogoutIcon from "../icons/Logout";



const styles = (theme) => ({
    ...getSharedStyles(theme),
    navbar: {
        height: '60px',
        width: '100%',
        position: 'fixed',
        top: '0',
        left: '0',
        zIndex: '1100',
        background: 'white',
        boxShadow: "0px 0px 17px 1px rgba(0,0,0,0.08)",
        [theme.breakpoints.up("lg")]: {
            height: '70px',
        },
    },
    navbarLogged: {
        [theme.breakpoints.up("lg")]: {
            height: '90px',
        },
    },
    navbarDark: {
        [theme.breakpoints.up("lg")]: {
            boxShadow: 'none',
            background: theme.palette.secondary.main,
        },
    },
    menuIconHolder: {
        background: theme.palette.primary.main,
        height: "60px",
        width: "60px",
    },  
    adminMenuIconHolder: {
        background: '#33d7f5',
        height: "70px",
        width: "70px",
        position: 'absolute',
        top: 0,
        left: 0,
    },   
    logoImg: {
        height: '20px',
        width: "auto",
    },
    "@media screen and (max-width: 350px)": {
        logoImg: {
            height: '17px',
            width: "auto",
        },
    },
    iconCount: {
        position: "absolute",
        color: "white",
        background: theme.palette.primary.main,
        borderRadius: "20px",
        padding: "2px 5px",
        fontSize: "12px",
        minWidth: "19px",
        textAlign: "center",
        top: "-3px",
        right: "-2px",
    },
    navItemDesktopActive: {
        position: 'relative',
        boxShadow: `inset 0 -4px 0 ${theme.palette.primary.main}`,
    },
    dropMenu: {
        position: "absolute",
        zIndex: "1200",
        minWidth: "150px",
        background: theme.palette.secondary.main,
        top: "calc(100% + 2px)",
        left: "-17px",
        transition: `opacity 150ms ease-in-out`,
        opacity: '0',
    },
    dropMenuItem: {
        fontFamily: "'IBM Plex Mono', monospace",
        fontSize: "12px",
        color: "white",
        padding: "13px 15px",
    },
    dropMenuSeparator: {
        width: "100%",
        border: "0",
        borderTop: "1px solid rgba(0,0,0,0.2)",
        margin: "0",
    },
});

function Navbar(props) {
    const { classes, notificationItems, cartItems, user } = props;
    const [menuOpen, setMenuOpen] = useState(false);
    
    const closeMenu = () => {
        setMenuOpen(false);  
    }

    const toggleMenu = () => { 
        setMenuOpen(!menuOpen);       
    };    

    const transitionStyles = {
        entering: { opacity: 1 },
        entered:  { opacity: 1 },
        exiting:  { opacity: 0 },
        exited:  { opacity: 0 },
    };

    let dark = true;      
    return (  
        <>
            <div className={classNames(classes.navbar, classes.w100, user && classes.navbarLogged, dark && classes.navbarDark)}>
                <div className={classNames(classes.container, classes.h100, classes.p0, classes.pxLg5)}>
                    <div className={classNames(classes.dFlex, classes.w100, classes.h100, classes.pr3, classes.prLg0, classes.justifyContentLgBetween)}>
                        {(user && (user.permissions & permissions.doby)) &&
                            <div
                            onClick={props.handleSidebarOpen}
                            className={classNames(
                                classes.adminMenuIconHolder,
                                classes.dNone,
                                classes.dLgFlex,                           
                                classes.alignItemsCenter,
                                classes.justifyContentCenter,                            
                            )}>
                            <MenuIcon className={classNames(classes.textWhite)} size={33} />
                        </div>
                        }
                        <div
                            onClick={props.handleSidebarOpen}
                            className={classNames(
                                classes.menuIconHolder,
                                classes.dFlex,
                                classes.dLgNone,
                                classes.alignItemsCenter,
                                classes.justifyContentCenter,                            
                            )}>
                            <MenuIcon className={classNames(classes.textWhite)} size={33} />
                        </div>
                        <div className={classNames(classes.logoHolder, classes.dFlex, classes.alignItemsCenter, classes.ml4, classes.mlLg0)}>
                            <img src={`/img/${config.locale}/logo.png`} className={classNames(classes.logoImg, dark && classes.dLgNone) } alt={"logo"} />
                            {dark && <img src={`/img/${config.locale}/logo-light.png`} className={classNames(classes.logoImg, classes.dNone, classes.dLgBlock)} alt={"logo"} />}
                        </div>

                        {user && <div className={classNames(classes.dNone, classes.dLgFlex, classes.alignItemsCenter)}>
                            <a className={classNames(classes.dFlex, classes.flexCol, classes.alignItemsCenter, classes.justifyContentCenter, classes.mx4, classes.h100, classes.navItemDesktopActive)}>
                                <HomeIcon className={classNames(dark ? classes.textWhite : classes.textSecondary)} size={30} />
                                <div className={classNames(classes.textMono, classes.textXs, dark ? classes.textWhite : classes.textSecondary)}>Komunita</div>
                            </a>
                            <a className={classNames(classes.dFlex, classes.flexCol, classes.alignItemsCenter, classes.justifyContentCenter, classes.mx4, classes.h100)}>
                                <MedalIcon className={classNames(dark ? classes.textWhite : classes.textSecondary)} size={30} />
                                <div className={classNames(classes.textMono, classes.textXs, dark ? classes.textWhite : classes.textSecondary)}>Vyzvy</div>
                            </a>
                            <a className={classNames(classes.dFlex, classes.flexCol, classes.alignItemsCenter, classes.justifyContentCenter, classes.mx4, classes.h100)}>
                                <ShirtIcon className={classNames(dark ? classes.textWhite : classes.textSecondary)} size={30} />
                                <div className={classNames(classes.textMono, classes.textXs, dark ? classes.textWhite : classes.textSecondary)}>E-shop</div>
                            </a>
                            <div 
                                className={classNames(classes.dFlex, classes.flexCol, classes.alignItemsCenter, classes.justifyContentCenter, classes.mx4, classes.h100, classes.positionRelative)} 
                                tabIndex="-1"
                                onBlur={closeMenu}                              
                            >
                                <div className={classNames(classes.dFlex, classes.flexCol, classes.alignItemsCenter)} onClick={toggleMenu}>
                                    <MoreDotsIcon className={classNames(dark ? classes.textWhite : classes.textSecondary)} size={30} />
                                    <div className={classNames(classes.textMono, classes.textXs, dark ? classes.textWhite : classes.textSecondary)}>
                                        {menuOpen ? "Menej" : "Viac"}
                                    </div>
                                </div>

                                <Transition in={menuOpen} timeout={150} unmountOnExit={true}>
                                    {state=> (
                                        <div className={classNames(classes.dropMenu)} style={{ ...transitionStyles[state]}} >
                                            <div className={classNames(classes.dFlex, classes.flexCol)}>
                                                <div className={classNames(classes.dFlex, classes.alignItemsCenter, classes.dropMenuItem)}>
                                                    <ShirtIcon className={classNames(classes.textWhite, classes.mr2)} size={35} />
                                                    E-shop
                                                </div>
                                                <hr className={classes.dropMenuSeparator} />
                                                <div className={classNames(classes.dFlex, classes.alignItemsCenter, classes.dropMenuItem, classes.textOrange400)}>
                                                    <LogoutIcon className={classNames(classes.textOrange400, classes.mr2)} size={35} />
                                                    Odhlsenie
                                                </div>                                                
                                            </div>
                                        </div>
                                    )}
                                </Transition>
                            </div>                         
                        </div>}

                        <div className={classNames(classes.mlAuto, classes.mlLg0, classes.dFlex, classes.alignItemsCenter, classes.justifyContentEnd)}>
                            {user && 
                                <div
                                    className={classNames(
                                        classes.iconCounterHolder,
                                        classes.dFlex,
                                        classes.alignItemsCenter,
                                        classes.positionRelative,
                                        classes.mr2
                                    )}
                                    onClick={() => props.history.push("/notifications")}
                                >
                                    <NotificationIcon
                                        className={classNames(dark ? classes.textWhite : classes.textSecondary, classes.dNone, classes.dLgBlock)}                                
                                        size={33}
                                    />   
                                    <NotificationIcon
                                        className={classNames(classes.textSecondary, classes.dLgNone)}                                
                                        size={33}
                                    />                                  
                                    {notificationItems > 0 && <div className={classNames(classes.iconCount)}>{notificationItems}</div>}
                                </div>
                            }

                            <div
                                className={classNames(
                                    classes.iconCounterHolder,
                                    classes.dFlex,
                                    !user && classes.dLgNone,
                                    classes.alignItemsCenter,
                                    classes.positionRelative,
                                    classes.mr2
                                )}
                                onClick={() => props.history.push("/eshop/cart")}
                            >
                                <BackpackIcon
                                    className={classNames(dark ? classes.textWhite : classes.textSecondary, classes.dNone, classes.dLgBlock)}                                
                                    size={33}
                                />  
                                <BackpackIcon
                                    className={classNames(classes.textSecondary, classes.dLgNone)}                                
                                    size={33}
                                />                                
                                {cartItems > 0 && <div className={classNames(classes.iconCount)}>{cartItems}</div>}
                            </div>

                            {!user && 
                                <div
                                    className={classNames(                               
                                        classes.dFlex,
                                        classes.dLgNone,
                                        classes.alignItemsCenter,
                                        classes.positionRelative,                                    
                                    )}
                                    onClick={() => props.history.push("/login")}
                                >
                                    <ProfileIcon
                                        className={classNames(classes.textSecondary)}                                
                                        size={33}
                                    />                            
                                </div>
                            }
                        </div>                        

                        {!user && <div className={classNames(classes.mlAuto, classes.dNone, classes.dLgFlex, classes.alignItemsCenter)}>
                            <a className={classNames(classes.textMono, classes.textM, classes.mr7)}>Domov</a>
                            <a className={classNames(classes.textMono, classes.textM, classes.mr7)}>E-shop</a>
                            <a className={classNames(classes.textMono, classes.textM, classes.mr7)}>Najastejie otzky</a>
                            <a className={classNames(classes.textMono, classes.textM, classes.dFlex, classes.alignItemsCenter)}>
                                Prihlsenie
                                <ProfileIcon
                                    className={classNames(classes.textSecondary, classes.ml2)}                                
                                    size={25}
                                /> 
                            </a>
                        </div>}
                    </div>
                </div>
            </div>
        </>
    );
}

export default compose(withStyles(styles), withLocalization, withRouter)(Navbar);
