import React from "react";
import { withStyles } from "@material-ui/core/styles";
import compose from "../../../compose";

import classNames from "classnames";
import getSharedStyles from "../../../style";

const styles = (theme) => ({
  ...getSharedStyles(theme),
  socialConnectCard: {
    background: "white",
  },
  socialIcon: {
    width: "30px",
    height: "30px",
  },
});

function StravaConnection(props) {
  const { classes } = props;

  const handleClick = () => {
    const url = `https://accounts.google.com/o/oauth2/v2/auth?scope=https://www.googleapis.com/auth/userinfo.profile https://www.googleapis.com/auth/userinfo.email&redirect_uri=${process.env.REACT_APP_HOMEPAGE}?provider=2&response_type=code&client_id=${props.config.id}`;
    window.location.replace(url);
  };

  if (!props.config) {
    return null;
  }

  return (
    <>
      <div
        className={classNames(
          classes.p3,
          classes.socialConnectCard,
          classes.roundedMd,
          classes.dBlock,
          props.className
        )}
        onClick={handleClick}
      >
        <div
          className={classNames(
            classes.dFlex,
            classes.w100,
            classes.alignItemsCenter
          )}
        >
          <img
            src={"./img/strava-logo.png"}
            className={classNames(classes.socialIcon)}
          />
          <div className={classNames(classes.flexCol, classes.ml3)}>
            <div className={classNames()}>Prida a prepoji Google</div>
            {/* <div className={classNames(classes.textM2, classes.text300)}>
                            Njs priateov a prida link na profil
                        </div> */}
          </div>
        </div>
      </div>
    </>
  );
}

export default compose(withStyles(styles))(StravaConnection);
