import React from "react";
import { withStyles } from "@material-ui/core/styles";
import classNames from "classnames";
import compose from "../../../compose";

const styles = (theme) => ({
    icon: {
        height: "20px",
        width: "auto",
    },
});

function Icon(props) {
    const { classes } = props;
    return (
        <>
            <svg
                xmlns="http://www.w3.org/2000/svg"
                width="100%"
                height="100%"
                fill="none"
                viewBox="0 0 32 32"
                className={classNames(classes.icon, props.className)}
                style={{ height: `${props.size}px` }}>
                <path fill="currentColor" stroke="currentColor" strokeWidth="1.5" d="M8 26h4.5v-4.5l1-1h4l1 1V26H23l2-2V13l-9.5-7.5L6 13v11l2 2z"></path>
            </svg>
        </>
    );
}

export default compose(withStyles(styles))(Icon);
