import React, { useState, useEffect } from 'react';
import { withStyles } from '@material-ui/core/styles';
import getSharedStyles from '../../../style';
import classNames from 'classnames';
import { withLocalization } from '../../shared/localization'
import compose from '../../../compose';
import CheckoutStepper from '../../dobyComponents/CheckoutStepper'
import Grid from '@material-ui/core/Grid';
import Button from '../../dobyComponents/form/Button';
import withDoby from '../../../consumers/withDoby';
import Faq from "../../dobyComponents/Faq";
import { formatPrice } from '../../../utils/helpers';
import ChallengePackageSubproduct from './ChallengePackageSubproduct'

import Markdown from "../../shared/Markdown";

const styles = theme => ({
    ...getSharedStyles(theme),
    stepNumber: {
        height: '40px',
        width: '40px',
        minWidth: '40px',
        textAlign: 'center',
        fontWeight: '700',
        fontSize: '20px',
        lineHeight: '40px',
        background: theme.palette.secondary.main,
        color: 'white',
        borderRadius: '25px'
    },
    packageCard: {
       background: 'white'
    },
    packageLogoHolder: {
        width: '100px',
    },
    packageLogo: {
        width: '100%',
        height: 'auto'
    },
    packageContentIcon: {
        fontSize: '20px'
    }
});

function ChallengePackage(props) {
    const { classes } = props;
    const [logo, setLogo] = useState(null);
    const [challenge, setChallenge] = useState(null);
    const [packs, setPacks] = useState([]);

    useEffect(() => {
        const fetchData = async () => {
            const { _id } = props.match.params;
            const { data } = await props.doby.getChallenges();
            const challenge = data.find(x => x._id === _id);

            if (challenge) {
                setChallenge(challenge);

                //NOTE: get packs from challenge
                if (challenge.packsRef && challenge.packsRef.length > 0) {

                    //NOTE: get packs
                    const packs = await props.doby.getProducts([
                        { field: "_id", op: "in", value: challenge.packsRef.map(x => x.id) },
                        { field: "disabled", value: false}
                    ]);

                    //NOTE: if packs contains eny subproducts we need to get them
                    if (packs.data.length > 0) {
                        for (let pack of packs.data) {
                            if (pack.productsRef && pack.productsRef.length > 0) {
                                const subProducts = await props.doby.getProducts([
                                    { field: "_id", op: "in", value: pack.productsRef.map(x => x.id) },
                                    { field: "disabled", value: false}
                                ]);

                                pack.subProducts = subProducts.data;
                            } else {
                                pack.subProducts = [];
                            }
                        }

                        setPacks(packs.data);
                    }
                }

                try {
                    const data = await props.doby.getFile(challenge.logoDarkId);
                    var reader = new FileReader();
                    reader.onloadend = () => {
                        setLogo(reader.result);
                    }
                    reader.readAsDataURL(data);
                }
                catch (err) {
                    console.error(err)
                }
            }

            await props.doby.getPosts([{ field: "type.key", value: 10 }], {
                field: "title",
                dir: "asc",
            });
        }

        fetchData();
    }, []);

    const nextStep = (selectedPack) => {
        if (!challenge || !selectedPack)
            return;

        props.doby.cartAddPack({
            ...selectedPack,
            challengeRef: { id: challenge._id }
        });

        //TODO: skip delivery data when only one package wo products is selected
        // const { products } = props.doby.cart.data;
        // const doesPackageNeedDelivery = (products.length === 1 && products[0].product.catalog_id === "pack" && products[0].product.productsRef.length === 1);
        // console.log(doesPackageNeedDelivery);
        
        props.history.push(`/challenge-checkout-equipment/${challenge._id}`);
    }

    const getLeftCount = (pack) => {
        const count = pack.max_order_count - pack.order_count;
        if(count <= 0) {
            return `${pack.unavailable_text ? pack.unavailable_text : props.translate('out_stock')}`;
        }

        let currentLessThen = 0;
        const lessThen = [10, 100, 1000];
        for(let currCnt of lessThen) { 
            if(count < currCnt) {
                currentLessThen = currCnt;
                break;
            }
        }

        return `${props.translate('challenge_checkout.left')} ${props.translate('less_then')} ${currentLessThen} ks`;
    }

    return (
        <>
            <CheckoutStepper className={classNames(classes.mt5)} currentState={1} />

            <div className={classNames(classes.bgLight)}>
                <div className={classNames(classes.container)}>
                    <Grid
                        container
                        direction="row"
                        justify="flex-start"
                    >
                        <Grid item xs={12} className={classNames(classes.mt7, classes.mb6)}>
                            <div className={classNames(classes.dFlex, classes.alignItemsCenter)}>
                                <div className={classNames(classes.stepNumber)}>
                                    1
                                </div>
                                <div className={classNames(classes.dFlex, classes.flexCol, classes.ml4)}>
                                    <h3 className={classNames(classes.uppercase)}>
                                        {props.translate('challenge_checkout.starter')}
                                    </h3>
                                    <div className={classNames(classes.textMono, classes.fontMedium, classes.textGray600)}>
                                        {challenge && challenge.name}
                                    </div>
                                </div>
                            </div>
                        </Grid>
                    </Grid>
                </div>

                <div className={classNames(classes.container, classes.pb10, classes.mt5)}>
                    <Grid
                        container
                        direction="row"
                        justify="flex-start"
                        spacing={24}
                    >
                        {packs.map(pack => {
                            return (
                                <Grid item xs={12} sm={6} className={classNames(classes.mb3)} key={pack._id}>
                                    <div className={classNames(classes.packageCard)}>
                                        <div className={classNames(classes.p6)}>
                                            <div className={classNames(classes.dFlex, classes.w100, classes.alignItemsCenter)}>
                                                <div className={classNames(classes.dFlex, classes.flexCol, classes.flexGrow)}>
                                                    <h4 className={classNames(classes.textMono, classes.uppercase)}>
                                                        {pack && pack.name}
                                                    </h4>
                                                    <h5 className={classNames(classes.mt1)}>
                                                        {props.translate('challenge_checkout.starter')}
                                                        {/* {pack && pack.short_description} */}
                                                    </h5>
                                                    <div className={classNames(classes.textPrimary, classes.textMono, classes.fontSemibold, classes.mt4)}>
                                                        {pack && <> {getLeftCount(pack)}</>}
                                                    </div>
                                                    <div className={classNames(classes.fontMedium, classes.mt1)}>
                                                        {pack && formatPrice(pack.price_with_tax)} <span className={classNames(classes.textM, classes.textGray400)}>{props.translate('with_tax')}</span>
                                                    </div>
                                                </div>
                                                <div className={classNames(classes.packageLogoHolder)}>
                                                    <img src={logo} className={classNames(classes.packageLogo)} alt="" />
                                                </div>
                                            </div>

                                            <div className={classNames(classes.dFlex, classes.flexCol, classes.mt4)}>
                                                <Markdown>{pack && pack.long_description ? pack.long_description : ''}</Markdown>

                                                {/* <div className={classNames(classes.dFlex, classes.w100, classes.alignItemsCenter)}>
                                                    <div className={classNames(classes.packageContentIcon)}></div>
                                                    <div className={classNames(classes.ml3, classes.textMono, classes.textGray700, classes.fontMedium, classes.textM)}>
                                                        {props.translate('challenge_checkout.registration')}
                                                    </div>
                                                </div>
                                                <div className={classNames(classes.dFlex, classes.w100, classes.alignItemsCenter, classes.mt2)}>
                                                    <div className={classNames(classes.packageContentIcon)}></div>
                                                    <div className={classNames(classes.ml3, classes.textMono, classes.textGray700, classes.fontMedium, classes.textM)}>
                                                        {props.translate('challenge_checkout.access')}
                                                    </div>
                                                </div>
                                                <div className={classNames(classes.dFlex, classes.w100, classes.alignItemsCenter, classes.mt2)}>
                                                    <div className={classNames(classes.packageContentIcon)}></div>
                                                    <div className={classNames(classes.ml3, classes.textMono, classes.textGray700, classes.fontMedium, classes.textM)}>
                                                        {props.translate('challenge_checkout.tree')}
                                                    </div>
                                                </div>
                                                <div className={classNames(classes.dFlex, classes.w100, classes.alignItemsCenter, classes.mt2)}>
                                                    <div className={classNames(classes.packageContentIcon)}></div>
                                                    <div className={classNames(classes.ml3, classes.textMono, classes.textGray700, classes.fontMedium, classes.textM)}>
                                                        {props.translate('challenge_checkout.protection')}
                                                    </div>
                                                </div>
                                                <div className={classNames(classes.dFlex, classes.w100, classes.alignItemsCenter, classes.mt2)}>
                                                    <div className={classNames(classes.packageContentIcon)}></div>
                                                    <div className={classNames(classes.ml3, classes.textMono, classes.textGray700, classes.fontMedium, classes.textM)}>
                                                        {props.translate('challenge_checkout.medal')}
                                                    </div>
                                                </div> */}
                                            </div>

                                            <div className={classNames(classes.dFlex, classes.flexCol, classes.mt5)}>
                                                { pack.subProducts.map((subproduct) => { 
                                                    if(subproduct.catalog_id !== 'challenge') {
                                                        return (
                                                            <ChallengePackageSubproduct
                                                                product={subproduct}
                                                            />
                                                        );
                                                    }  
                                                    return                                                                                     
                                                })} 
                                            </div>    
                                        </div>  

                                                                                                             

                                        {/* {pack.name === 'Limitka' && pack.subProducts.forEach((subproduct) => {   
                                            //subproduct.catalog_id !== 'challenge'                                         
                                            return (
                                                <div key={subproduct._id}>
                                                    <p>{subproduct.name}</p>
                                                </div>
                                            );
                                        })} */}

                                        <Button
                                            className={classNames()}
                                            fullwidth
                                            onClick={()=> nextStep(pack)}
                                            arrow
                                            disabled={ pack && pack.max_order_count !== 0 && (pack.max_order_count - pack.order_count) <= 0}
                                            style={{maxWidth: 'none'}}
                                        >
                                            {props.translate('continue')}
                                        </Button>
                                    </div>
                                </Grid>)
                        })
                        }

                        {/* <Grid item xs={12} className={classNames(classes.mt4)}>
                            <Button
                                className={classNames()}
                                fullwidth
                                onClick={nextStep}
                                arrow
                                disabled={!selectedPack || (selectedPack.max_order_count - selectedPack.order_count) <= 0}
                            >
                                {props.translate('continue')}
                            </Button>
                        </Grid> */}                        
                    </Grid>
                </div>
            </div>  

            <div className={classNames(classes.container, classes.pb10, classes.pt10)}>
                <Grid item xs={12}>
                    <h3 className={classNames(classes.uppercase)}>
                        {props.translate('questions.title')}
                    </h3>
                    <div className={classNames(classes.textGray500, classes.mt1, classes.mb5)}>
                        {props.translate('questions.sub_title')}
                    </div>
                    <Faq questions={props.doby.posts.data} />
                </Grid>
            </div>          
        </>
    )
}

export default compose(
    withStyles(styles),
    withLocalization,
    withDoby
)(ChallengePackage);