import React from "react";
import { withStyles } from "@material-ui/core/styles";
import classNames from "classnames";
import compose from "../../../compose";

const styles = (theme) => ({
    icon: {
        height: "20px",
        width: "auto",
    },
});

function Icon(props) {
    const { classes } = props;
    return (
        <>
            <svg
                xmlns="http://www.w3.org/2000/svg"
                width="100%"
                height="100%"
                fill="none"
                viewBox="0 0 32 32"
                className={classNames(classes.icon, props.className)}
                style={{ height: `${props.size}px` }}>
                <g stroke="currentColor" strokeWidth="1.5" clipPath="url(#clip0)">
                    <path d="M9 23.526v-7l3.5-3.5h7l3.5 3.5v7l-3.5 3.5h-7l-3.5-3.5z"></path>
                    <path d="M13 21.526v-3l1.5-1.5h3l1.5 1.5v3l-1.5 1.5h-3l-1.5-1.5zM14.437 11.799v5.448m3.121-5.448v5.448M13.683 11.646h4.63M14.93 12.256l-3.848-8.971m5.99 8.971l3.849-8.971"></path>
                </g>
                <defs>
                    <clipPath id="clip0">
                        <path fill="currentColor" d="M0 0H32V32H0z"></path>
                    </clipPath>
                </defs>
            </svg>
        </>
    );
}

export default compose(withStyles(styles))(Icon);
