import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import classNames from 'classnames';
import compose from '../../../compose';

const styles = theme => ({   
    icon: {
        height: '20px',
        width: 'auto'
    }
});

function FacebookIcon(props) {
    const { classes } = props;
    return (
        <>            
            <svg width="100%" height="100%" viewBox="0 0 32 32" fill="none" xmlns="http://www.w3.org/2000/svg" className={classNames(classes.icon, props.className)} style={{height: `${props.size}px`}}>
            <path
                fill="currentColor"
                fillRule="evenodd"
                d="M17.945 29.866C24.756 28.919 30 23.072 30 16c0-7.732-6.268-14-14-14S2 8.268 2 16c0 6.9 4.992 12.635 11.562 13.788v-9.506H10V16.33h3.562v-3.012C13.562 9.893 15.655 8 18.86 8c1.535 0 3.14.267 3.14.267v3.364h-1.77c-1.742 0-2.285 1.055-2.285 2.136v2.563h3.89l-.621 3.952h-3.269v9.584z"
                clipRule="evenodd"
            ></path>
            </svg>
        </>
    )
}

export default compose(
    withStyles(styles)
)(FacebookIcon);