import React from "react";
import Table from "../shared/table/Table";
import FormDialog from "../shared/FormDialog";
import Form from "./Form";

import compose from "../../compose";
import withStore from "../../consumers/withStore";
import withUser from "../../consumers/withUser";
import { withLocalization } from "../shared/localization";

class TableWrapper extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      x: null,
      isOpen: false,
    };
  }

  async componentDidMount() {
    try {
      const { getEnums } = this.props.store;
      const filter = [
        { field: "enumTypeId", value: 5 },
        { field: "disabled", value: false },
      ];
      await getEnums(filter);
    } catch (e) {
      console.error(e);
    }
  }

  handleChangeClient = (name, value) => {
    this.setState({ clientId: value });
  };

  handleClose = () => {
    this.setState({ isOpen: false });
  };

  handleOpen = () => {
    this.setState({ isOpen: true });
  };

  handleChange = async (filters, page, sort, render) => {
    try {
      return await this.props.store.getTransports(filters, sort, page, render);
    } catch (error) {
      this.props.user.showError(error);
    }
  };

  getCountry = (n) => {
    const { enums } = this.props.store;

    const found = enums.data.find((e) => e._id === n.countryId);
    return found ? found.name : "";
  };

  generateRows = () => {
    return [
      { field: "name", disablePadding: true },
      { field: "price", type: "num" },
      { field: "countryId", cell: this.getCountry },
      { field: "key", type: "num" },
      { field: "disabled", type: "bool" },
    ];
  };

  onEdit = async (x) => {
    this.setState({ x, isOpen: true });
  };

  isDisabled = (x) => {
    return false;
  };

  render() {
    const { deleteTransport } = this.props.store;
    const { translate } = this.props;

    return (
      <div>
        <Table
          rows={this.generateRows()}
          data="transports"
          onChange={this.handleChange}
          onDelete={deleteTransport}
          title={translate("transports")}
          onEdit={this.onEdit}
          store={this.props.store}
          filters={[]}
          isDisabled={this.isDisabled}
          filename="transports"
        />
        <FormDialog
          title={translate("transport")}
          onClose={this.handleClose}
          isOpen={this.state.isOpen}
        >
          <Form data={this.state.x} onSubmit={this.handleClose} />
        </FormDialog>
      </div>
    );
  }
}

export default compose(withStore, withUser, withLocalization)(TableWrapper);
