import React from "react";
import { withStyles } from "@material-ui/core/styles";
import classNames from "classnames";
import compose from "../../../compose";

const styles = (theme) => ({
    icon: {
        height: "20px",
        width: "auto",
    },
});

function Icon(props) {
    const { classes } = props;
    return (
        <>
            <svg
                xmlns="http://www.w3.org/2000/svg"
                width="100%"
                height="100%"
                fill="none"
                viewBox="0 0 32 32"
                className={classNames(classes.icon, props.className)}
                style={{ height: `${props.size}px` }}>
                <path stroke="currentColor" strokeWidth="1.5" d="M6 21V11l5-5h10l5 5v10l-5 5H11l-5-5z"></path>
                <path
                    stroke="currentColor"
                    strokeWidth="1.5"
                    d="M21.5 25.5V23l-2.772-2v-1l1-2 1-1v-2l-1-1v-2l-1.5-1.5h-1.5l-1-.5h-1.5l-2 2v2l-1 1v2l1 1 1 2v1L10.5 23v2.5"></path>
            </svg>
        </>
    );
}

export default compose(withStyles(styles))(Icon);
