import React, {useEffect, useState} from 'react';
import { withStyles } from '@material-ui/core/styles';
import getSharedStyles from '../../../style';
import classNames from 'classnames';
import { withLocalization } from '../../shared/localization'
import compose from '../../../compose';
import withDoby from '../../../consumers/withDoby';

const styles = theme => ({
    ...getSharedStyles(theme),
    thumbnailHolder: {
        height: '70px',
        width: '70px',             
        overflow: 'hidden',
        position: 'relative',
        // border: '1px solid #E4EAF3',
        // padding: '3px'
    },
    productThumbnail: {
        objectFit: 'contain',
        position: 'absolute',
        top: '0',
        left: '0',
        width: '100%',
        height: '100%',
        zIndex: '0',
    },
});

function ChallengePackageSubproduct(props) {
    const { classes, product } = props;

    const [img, setImg] = useState(null);

    useEffect( () => {        
        const getImage = async () => {
            try {
                if(product) {
                    console.log(product.thumbnailId)
                    const data = await props.doby.getFile(product.thumbnailId);
                    var reader = new FileReader();
                    reader.onloadend = () => {
                        setImg(reader.result);
                    }
                    reader.readAsDataURL(data);
                }
            } catch(e) {
                console.error(e);
            }
        }

        getImage();
    }, []);

    return (
        <>
            <div className={classNames(classes.packProducts, classes.dFlex, classes.alignItemsCenter, classes.w100)}>
                <div className={classNames(classes.thumbnailHolder)}>
                    <img
                        className={classes.productThumbnail}
                        src={img || ''} alt=""
                    />
                </div>
                <div className={classNames(classes.textMono, classes.fontSemibold, classes.textM, classes.ml2)}>
                    {product.name}
                </div>

            </div>
        </>
    )
}

export default compose(
    withStyles(styles),
    withLocalization,
    withDoby
)(ChallengePackageSubproduct);