import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import classNames from 'classnames';
import compose from '../../../compose';

const styles = theme => ({
    icon: {
        height: '20px',
        width: 'auto'
    }
});

function Icon(props) {
    const { classes } = props;
    return (
        <>            
            <svg
                xmlns="http://www.w3.org/2000/svg"
                width="100%"
                height="100%"
                fill="none"
                viewBox="0 0 32 32"
                className={classNames(classes.icon, props.className)} style={{height: `${props.size}px`}}
            >
                <path
                    fill="#F5333F"
                    stroke="#F5333F"
                    strokeWidth="3.76"
                    d="M24.42 8.57a5.315 5.315 0 00-1.727-1.187 5.194 5.194 0 00-4.075 0 5.316 5.316 0 00-1.727 1.188l-1.026 1.055L14.84 8.57a5.25 5.25 0 00-3.764-1.604A5.25 5.25 0 007.311 8.57a5.556 5.556 0 00-1.56 3.872c0 1.452.561 2.845 1.56 3.871l1.025 1.056 7.53 7.744 7.528-7.744 1.026-1.056a5.487 5.487 0 001.154-1.776 5.609 5.609 0 000-4.191A5.487 5.487 0 0024.42 8.57z"
                ></path>
            </svg>
        </>
    )
}

export default compose(
    withStyles(styles)
)(Icon);