import React from "react";
import { withStyles } from "@material-ui/core/styles";
import compose from "../../../../compose";

import classNames from "classnames";
import getSharedStyles from "../../../../style";

import GoogleIcon from "../../icons/Google";
import { withLocalization } from "../../../shared/localization";

const styles = (theme) => ({
    ...getSharedStyles(theme),
    socialConnectCard: {
        background: "white",
    },    
});

function StravaConnection(props) {
    const { classes, onlyIcon } = props;

    const handleClick = () => {
        const url = `https://accounts.google.com/o/oauth2/v2/auth?scope=https://www.googleapis.com/auth/userinfo.profile https://www.googleapis.com/auth/userinfo.email&redirect_uri=${process.env.REACT_APP_HOMEPAGE}?provider=2&response_type=code&client_id=${props.config.id}`;
        window.location.replace(url);
    };

    if (!props.config) {
        return null;
    }

    return (
        <>
            <div className={classNames(classes.p3, classes.socialConnectCard, classes.dBlock, props.className)} onClick={handleClick}>
                <div className={classNames(classes.dFlex, classes.w100, classes.alignItemsCenter, onlyIcon && classes.justifyContentCenter)}>
                    <GoogleIcon size={30} className={classNames()} />
                    {!onlyIcon && <div className={classNames(classes.flexCol, classes.ml4)}>
                        <div className={classNames(classes.fontMedium)}>{props.translate("googleRegister")}</div>
                    </div>}
                </div>
            </div>
        </>
    );
}

export default compose(withStyles(styles), withLocalization)(StravaConnection);
