import React from "react";
import PropTypes from "prop-types";
import { withRouter } from "react-router";
import withStyles from "@material-ui/core/styles/withStyles";
import { withLocalization } from "../shared/localization";
import Grid from "@material-ui/core/Grid";
import TextField from "../dobyComponents/form/TextField";
import Button from "../dobyComponents/form/Button";
import classNames from "classnames";
import getSharedStyles from "../../style";
import { Redirect } from "react-router-dom";
import withUser from "../../consumers/withUser";
import withStore from "../../consumers/withStore";
import compose from "../../compose";
import config from "../../config";
import _ from "lodash";
import FbConnect from "../dobyComponents/socialConnections/login/FacebookConnection"
import GoogleConnect from "../dobyComponents/socialConnections/login/GoogleConnection"


const styles = (theme) => ({
    ...getSharedStyles(theme),     
    loginContent: {
        position: "relative",
        zIndex: "2",
    },
    socialLoginItem: {
        width: "100%",
        height: '50px'  ,
        background: 'red'     
    },
    loginSeparator: {
        flexGrow: "1",
        position: "relative",
        "&:after": {
            content: "''",
            position: "absolute",
            display: "block",
            width: "100%",
            height: "1px",
            background: "rgba(0, 0, 0, 0.08)",
            top: "50%",
        },
    },
    loginCol: {
        maxWidth: "450px",
    },
    loginBg: {
        backgroundImage: `url(/img/${config.locale}/intro-bg-desktop.jpg)`,
        backgroundRepeat: "no-repeat",
        backgroundColor: theme.palette.type === "light" ? theme.palette.grey[50] : theme.palette.grey[900],
        backgroundSize: "cover",
        backgroundPosition: "center",
    },
});

const INIT_STATE = {
    password: {
        value: "",
        errorText: "",
    },
    repeatedPassword: {
        value: "",
        errorText: "",
    },
    email: {
        value: "",
        isLoading: false,
        errorText: "",
    },
    disabled: false,
};

const buildState = () => {
    return {
        ...INIT_STATE,
    };
};

class Register extends React.Component {
    constructor(props) {
        super(props);

        this.state = buildState();
    }

    handleSubmit = async (e) => {
        e.preventDefault();

        const isValid = this.validPasswords() && (await this.validEmail());
        if (!isValid) {
            return;
        }

        const { email, password } = this.state;
        const user = {
            username: "u:" + email.value,
            name: email.value,
            config: config.configId,
            email: email.value,
            password: password.value,
        };

        await this.props.user.registerUser(user);
        this.props.history.push("/login");
    };

    handleChange = (name, value) => {
        this.setState((state) => _.set(state, name, value));
    };

    isValid = async (username) => {
        const ids = await this.props.user.checkUser(username);
        return ids.length === 0;
    };

    validEmail = async () => {
        const { translate } = this.props;
        const { email } = this.state;

        this.setState((state) => ({
            email: {
                ...state.email,
                isLoading: true,
            },
        }));

        const isValid = !email.value || (await this.isValid(email.value));
        const errorText = isValid ? "" : translate("field.emailInUse");

        this.setState((state) => ({
            email: { ...state.email, errorText, isLoading: false },
        }));

        return isValid;
    };

    validPasswords = () => {
        const { password, repeatedPassword } = this.state;
        const { translate } = this.props;

        const regex = new RegExp(config.passwordPattern);
        const isValidPassword = !password.value || regex.test(password.value);
        const errorTextPassword = isValidPassword ? "" : translate("field.passwordWeak");

        const isValidRepeatedPassword = password.value === repeatedPassword.value;
        const errorTextRepeatedPassword = isValidRepeatedPassword ? "" : translate("field.passwordNoMatch");

        this.setState((state) => ({
            password: { ...state.password, errorText: errorTextPassword },
            repeatedPassword: {
                ...this.state.repeatedPassword,
                errorText: errorTextRepeatedPassword,
            },
        }));

        return isValidPassword && isValidRepeatedPassword;
    };

    handleClickReset = async () => {
        const { username } = this.state;

        if (username) {
            try {
                await this.props.user.reset(username.trim().toLowerCase());
                this.props.user.showMsg("Mail sended.");
            } catch (error) {
                this.props.user.showError(error);
            }
        }
    };

    goToLogIn = () => this.props.history.push(`/login`);

    render() {
        const { classes, user, translate } = this.props;
        const { email, password, repeatedPassword, disabled, oAuth } = this.state;

        const { from } = this.props.location.state || {
            from: { pathname: config.home },
        };

        if (oAuth) return null;
        if (user.user) return <Redirect to={from} />;

        return (
                <Grid container className={classNames(classes.h100)}>
                    <Grid item xs={12} sm={7} md={6}>
                        
                        <div className={classNames(classes.bgLight, classes.pt10, classes.h100)}>

                            <Grid container className={classNames(classes.h100)} justify="center">
                                <Grid item xs={12} sm={10} md={8} lg={7} xl={5} className={classNames(classes.loginCol)}>
                                    <div className={classNames(classes.container, classes.h100, classes.loginContent)}>
                                        <h4 className={classNames(classes.uppercase)}>Sta sa sasou skvelej 14-tiscovej komunity</h4>

                                        <div className={classNames(classes.dFlex, classes.flexCol, classes.w100, classes.mt7)}>       
                                        <GoogleConnect config={config.google}/>                                
                                            <FbConnect config={config.fb} className={classNames(classes.mt2)}/>                                            
                                        </div>

                                        <div className={classNames(classes.dFlex, classes.w100, classes.my7)}>
                                            <div className={classNames(classes.loginSeparator)}></div>
                                            <div className={classNames(classes.textMono, classes.textM, classes.textGray300, classes.px3)}>
                                                {translate("or")}
                                            </div>
                                            <div className={classNames(classes.loginSeparator)}></div>
                                        </div>

                                        <form className={classes.form} onSubmit={this.handleSubmit}>
                                            <TextField
                                                onChange={this.handleChange}
                                                fullWidth
                                                required
                                                value={email.value}                                                
                                                field="email.value"
                                                disabled={disabled || email.isLoading}
                                                type="email"
                                                errorText={email.errorText}
                                                onBlur={this.validEmail}
                                            />

                                            <TextField
                                                onChange={this.handleChange}
                                                fullWidth
                                                required
                                                value={password.value}                                          
                                                field="password.value"
                                                disabled={disabled}
                                                type={"password"}
                                                onBlur={this.validPasswords}
                                                errorText={password.errorText}
                                                className={classNames(classes.mt3)}
                                            />
                                            <TextField
                                                onChange={this.handleChange}
                                                fullWidth
                                                required
                                                value={repeatedPassword.value}                                         
                                                field="repeatedPassword.value"
                                                disabled={disabled}
                                                type={"new-password"}
                                                onBlur={this.validPasswords}
                                                errorText={repeatedPassword.errorText}
                                                className={classNames(classes.mt3)}
                                            />                                            

                                            <Button
                                                className={classNames(classes.mt5, classes.loginBtn)}
                                                fullwidth                                        
                                                arrow
                                                type="submit"
                                                outline
                                            >
                                                {translate("doRegister")}
                                            </Button>

                                            <div className={classNames(classes.dFlex, classes.justifyContentCenter, classes.mt4)}>
                                                Mas uz ucet? 
                                                <span className={classNames(classes.textMono, classes.uppercase, classes.underline, classes.textPrimary, classes.fontBold, classes.ml2, classes.cursorPointer)} onClick={this.goToLogIn}>
                                                    Prihlas sa
                                                </span>                                                
                                            </div>
                                        </form>
                                    </div>                               
                                </Grid>                    
                            </Grid>
                        </div>                                  
                    </Grid>

                    <Grid item xs={12} sm={5} md={6} className={classNames(classes.dNone, classes.dSmBlock, classes.loginBg)}></Grid>
                </Grid>
            // <Grid container component="main" className={classnames(classes.root)}>
            //     {/* HEADER */}
            //     <Grid item xs={12} className={classNames(classes.mt5)}>
            //         <div className={classNames(classes.container)}>
            //             <div className={classNames(classes.dFlex, classes.w100, classes.alignItemsCenter, classes.justifyContentBetween)}>
            //                 <div
            //                     style={{
            //                         display: "flex",
            //                         alignItems: "center",
            //                         flexWrap: "wrap",
            //                     }}>
            //                     <ArrowLeftIcon onClick={this.goToLogIn} />
            //                     <span>{translate("back")}</span>
            //                 </div>
            //             </div>
            //         </div>
            //     </Grid>

            //     <CssBaseline />
            //     <Grid item xs={false} sm={4} md={7} className={classes.image} />
            //     <Grid item xs={12} sm={8} md={5} component={"div"} elevation={6} square>
            //         <div className={classes.paper}>
            //             <img alt="logo" className={classes.avatar} src={`/img/${config.locale}/logo.png`} />

            //             <GithubButton fullWidth disabled={disabled} title={translate("githubRegister")} config={config.github} />
            //             <GoogleButton fullWidth disabled={disabled} title={translate("googleRegister")} config={config.google} />
            //             <FbButton fullWidth disabled={disabled} title={translate("fbRegister")} config={config.fb} />
            //             <StravaButton fullWidth disabled={disabled} title={translate("stravaRegister")} config={config.strava} />

            //             <Typography variant="body1">{translate("or")}</Typography>

            //             <form className={classes.form} onSubmit={this.handleSubmit}>
            //                 <TextField
            //                     type="email"
            //                     value={email.value}
            //                     field="email.value"
            //                     onChange={this.handleChange}
            //                     disabled={disabled || email.isLoading}
            //                     onBlur={this.validEmail}
            //                     errorText={email.errorText}
            //                     fullWidth
            //                     required
            //                 />
            //                 <TextField
            //                     type="password"
            //                     value={password.value}
            //                     field="password.value"
            //                     onChange={this.handleChange}
            //                     disabled={disabled}
            //                     onBlur={this.validPasswords}
            //                     errorText={password.errorText}
            //                     fullWidth
            //                     required
            //                 />
            //                 <TextField
            //                     type="new-password"
            //                     value={repeatedPassword.value}
            //                     field="repeatedPassword.value"
            //                     onChange={this.handleChange}
            //                     disabled={disabled}
            //                     onBlur={this.validPasswords}
            //                     errorText={repeatedPassword.errorText}
            //                     fullWidth
            //                     required
            //                 />

            //                 <Button type="submit" fullWidth variant="contained" color="primary" className={classes.submit} disabled={disabled}>
            //                     {translate("doRegister")}
            //                 </Button>
            //             </form>
            //         </div>
            //     </Grid>
            // </Grid>
        );
    }
}

Register.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default compose(withStyles(styles), withRouter, withUser, withStore, withLocalization)(Register);
