import React from "react";
import { withStyles } from "@material-ui/core/styles";
import classNames from "classnames";
import compose from "../../../compose";

const styles = (theme) => ({
    icon: {
        height: "20px",
        width: "auto",
    },
});

function Icon(props) {
    const { classes } = props;
    return (
        <>
            <svg
                xmlns="http://www.w3.org/2000/svg"
                width="100%"
                height="100%"
                fill="none"
                viewBox="0 0 32 32"
                className={classNames(classes.icon, props.className)}
                style={{ height: `${props.size}px` }}>
                <path fill="currentColor" d="M24 14a2 2 0 100 4 2 2 0 000-4zM16 14a2 2 0 100 4 2 2 0 000-4zM8 14a2 2 0 100 4 2 2 0 000-4z"></path>
            </svg>
        </>
    );
}

export default compose(withStyles(styles))(Icon);
