import React from "react";
import { withStyles } from "@material-ui/core/styles";
import classNames from "classnames";
import compose from "../../../compose";

const styles = (theme) => ({
    icon: {
        height: "20px",
        width: "auto",
    },
});

function Icon(props) {
    const { classes } = props;
    return (
        <>
            <svg
                xmlns="http://www.w3.org/2000/svg"
                width="100%"
                height="100%"
                fill="none"
                viewBox="0 0 32 32"
                className={classNames(classes.icon, props.className)}
                style={{ height: `${props.size}px` }}>
                <path
                    stroke="currentColor"
                    strokeWidth="1.5"
                    d="M23 22.774V26H9v-3.226l3.473-2.58v-1.29L11.2 16.322l-1.273-1.29v-2.581l1.273-1.29V8.58L13.745 6h1.91l1.272.645h1.91l1.908 1.936v2.58l1.273 1.29v2.581l-1.273 1.29-1.272 2.581v1.29L23 22.774z"></path>
            </svg>
        </>
    );
}

export default compose(withStyles(styles))(Icon);
