import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import getSharedStyles from '../../style';
import classNames from 'classnames';
import { withLocalization } from '../shared/localization'
import compose from '../../compose';
import Button from "./form/Button";
import Grid from "@material-ui/core/Grid";
import config from "../../config";

const styles = theme => ({
   ...getSharedStyles(theme),
    medalBanner: {
        position: 'relative',
    },
    medalBannerImg: {
        position: 'absolute',
        top: '0',
        right:'40px',
        height: '400px',
        [theme.breakpoints.up("lg")]: {
            height: '110%',
            height: 'calc(100% + 25px)',
            right:'60px',
        },
        [theme.breakpoints.up("xl")]: {
            right: '120px'
        }
    },
});

const getSrc = (img) => {
    return `/img/${config.locale}/${img}`;
};

function MedalBanner(props) { 
   const {classes} = props;

       return (
           <>
                <div className={classNames(classes.container, classes.bgSecondary, classes.p11, classes.medalBanner, props.className)}>
                    <Grid container direction="row">
                        <Grid item md={5} className={classNames()}>
                            <h1 className={classNames(classes.uppercase, classes.textWhite)}>
                                <span>Ziskaj</span> <br/>
                                <span className={classNames(classes.textPrimary)}>zasluzenu</span> <br/>
                                <span className={classNames(classes.textPrimary)}>medailu</span>
                            </h1>
                            <div className={classNames(classes.textGray200, classes.textBlock, classes.mt3)}>
                                Bude ti zaslan po spenom ukonen vzvy.
                            </div>
                            <Button
                                className={classNames(classes.mt5)}                                
                                arrow
                                minWidth={250}
                            >
                                Viac info
                            </Button>
                        </Grid>                        
                    </Grid> 
                    <img src={getSrc('medal-render.png')} className={classNames(classes.medalBannerImg)}/>     
                </div>
           </>
       )
}

export default compose(
   withStyles(styles),
   withLocalization
)(MedalBanner);