import React from "react";
import { withStyles } from "@material-ui/core/styles";
import compose from "../../../compose";
import { withLocalization } from "../../shared/localization";
import withStore from "../../../consumers/withStore";
import withDoby from "../../../consumers/withDoby";
import withUser from "../../../consumers/withUser";
import config from '../../../config'

import classNames from "classnames";
import getSharedStyles from "../../../style";
import CheckoutStepper from "../../dobyComponents/CheckoutStepper";

import TextField from "../../dobyComponents/form/TextField";
import TelField from "../../dobyComponents/form/TelField";
import SelectField from "../../dobyComponents/form/SelectField";
import Checkbox from "../../dobyComponents/form/CheckboxField";

import Grid from "@material-ui/core/Grid";
import Radio from "@material-ui/core/Radio";
import Button from "../../dobyComponents/form/Button";

import CreditCard from "../../dobyComponents/icons/CreditCard";
import Exchange from "../../dobyComponents/icons/Exchange";

import { formatPrice /*, getCurrencySymbol*/ } from "../../../utils/helpers";

const styles = (theme) => ({
    ...getSharedStyles(theme),
    paymentIconHolder: {
        height: "40px",
        width: "40px",
        background: "#E4EAF3",
    },
});

class DeliveryPayment extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            transports: null,
            invoiceErrorTexts: {
                invoiceNameSurname: null,
                invoiceCountry: null,
                invoiceCity: null,
                invoicePsc: null,
                invoiceStreet: null,
                invoiceStreetNumber: null,
            },
            companyOrderErrorTexts: {
                ico: null,
                dic: null,
                icdph: null,
                bic: null,
                iban: null,
                accountOwner: null,
            },
            commonErrorTexts: {
                nameSurname: null,
                email: null,
                country: null,
                city: null,
                psc: null,
                street: null,
                streetNumber: null,
                tel: null,
            },
            errorTexts: {},
            isValid: false,
            onlyChallenge: false,
        };
    }

    async componentDidMount() {
        await this.fetchData();
    }

    fetchData = async () => {

        //NOTE: get enums ( countries )
        {
            const { getEnums } = this.props.store;
            const filter = [
                { field: "enumTypeId", value: 5 },
                { field: "disabled", value: false },
            ];

            await getEnums(filter);
        }

        //NOTE: get transports from user
        {
            const { user } = this.props.user;
            const { enums } = this.props.store;
            const foundCountry = enums.data.find((e) => e.key === user.countryId);

            const filter = [
                { field: "countryId", value: foundCountry._id },
                { field: "disabled", value: false },
            ];

            this.setState({ transports: await this.props.store.getTransports(filter) });
        }

        this.props.doby.cartGetProducts();
        this.fillFromUser();
        this.props.doby.cartGetProducts();

        const { cart } = this.props.doby;
        if (
            cart.data.products.length === 1 &&
            cart.data.products[0].product.catalog_id === "pack"
        ) {
            this.setState({ onlyChallenge: true });
        }
    };

    fillFromUser = () => {
        const { user } = this.props.user;
        const { enums } = this.props.store;

        if (!user) return;

        const found = enums.data.find((e) => e.key === user.countryId);

        //NOTE: prefill deliver order data from user profile
        this.props.doby.deliveryOrderFill({
            nameSurname: user.name,
            country: found ? found._id : "",
            city: user.city,
            email: user.email,
            tel: user.tel,
            psc: user.postCode,
        });

        //this.validateForm();
    };

    handleChangeCountry = async (field, value) => {
        this.props.doby.deliveryOrderChanged(field, value);

        const filter = [
            { field: "countryId", value },
            { field: "disabled", value: false },
        ];

        this.setState({ transports: await this.props.store.getTransports(filter) });
    };

    validateField = (key) => {
        const numericFields = ['psc', 'streetNumber', 'invoicePsc', 'invoiceStreetNumber'];
        const { deliveryData } = this.props.doby.checkout.data;
        let errorTexts = {
            ...this.state.commonErrorTexts,
            ...this.state.invoiceErrorTexts,
            ...this.state.companyOrderErrorTexts,
            ...this.state.errorTexts
        };
        
        if (key.indexOf("tel") > -1) {
            errorTexts[key] =
                deliveryData[key].length <= 4
                    ? this.props.translate("required_field")
                    : null;
        } else if (numericFields.includes(key)) {
            errorTexts[key] =
                deliveryData[key].length <= 0 || (deliveryData[key].length > 0 && isNaN(deliveryData[key]))
                    ? this.props.translate("required_field")
                    : null;
        }
        else {
            errorTexts[key] =
                deliveryData[key].length <= 0
                    ? this.props.translate("required_field")
                    : null;
        }

        const fieldsAreValid = Object.values(errorTexts).filter((v) => v).length === 0;
        const checkboxesAreChoosen = deliveryData.deliveryType && deliveryData.paymentMethod;
        this.setState({
            errorTexts,
            isValid: fieldsAreValid && checkboxesAreChoosen,
        });
        //this.setState({errorTexts}, ()=> this.validateForm());
    };

    validateAllFormFields = () => {
        const { deliveryData } = this.props.doby.checkout.data;
        let errorTexts = {
            ...this.state.commonErrorTexts,
            ...this.state.invoiceErrorTexts,
            ...this.state.companyOrderErrorTexts,
        };

        for (let key in this.state.commonErrorTexts) {
            if (key === "tel") {
                errorTexts[key] =
                    deliveryData[key].length <= 4
                        ? this.props.translate("required_field")
                        : null;
                continue;
            }

            errorTexts[key] =
                deliveryData[key].length <= 0
                    ? this.props.translate("required_field")
                    : null;
        }

        if (deliveryData.invoiceData) {
            for (let key in this.state.invoiceErrorTexts) {
                errorTexts[key] =
                    deliveryData[key].length <= 0
                        ? this.props.translate("required_field")
                        : null;
            }
        }

        if (deliveryData.companyOrder) {
            for (let key in this.state.companyOrderErrorTexts) {
                errorTexts[key] =
                    deliveryData[key].length <= 0
                        ? this.props.translate("required_field")
                        : null;
            }
        }

        const fieldsAreValid =
            Object.values(errorTexts).filter((v) => v).length === 0;
        const checkboxesAreChoosen =
            deliveryData.deliveryType && deliveryData.paymentMethod;

        //NOTE: if we have all needed fields we can continue
        this.setState(
            { errorTexts, isValid: fieldsAreValid && checkboxesAreChoosen },
            () => {
                if (fieldsAreValid) {
                    this.props.history.push(`/eshop/checkout-summary`);
                }
            }
        );
    };

    goToSummary = () => {
        this.validateAllFormFields();
    };

    render() {
        const { classes } = this.props;
        const { enums } = this.props.store;
        const { transports } = this.state;

        const deliveryOrderChanged = async (field, value) => {
            await this.props.doby.deliveryOrderChanged(field, value);
            this.validateField(field);
            //this.validateForm();
        };

        const { deliveryData } = this.props.doby.checkout.data;

        return (
            <>
                <CheckoutStepper className={classNames(classes.mt5)} currentState={3} />

                <div className={classNames(classes.bgLight)}>
                    <div className={classNames(classes.container)}>
                        <Grid container direction="row" justify="center">
                            <Grid
                                item
                                xs={12} md={8} lg={6}
                                className={classNames(classes.mt7, classes.mb6)}
                            >
                                <div className={classNames(classes.dFlex, classes.flexCol)}>
                                    <h3 className={classNames(classes.uppercase)}>
                                        {this.props.translate("delivery_details")}
                                    </h3>
                                    <div className={classNames(classes.textMono, classes.fontMedium, classes.textGray600)}>
                                        {this.props.translate("for_medal_and_equip")}
                                    </div>
                                </div>
                            </Grid>
                        </Grid>
                    </div>
                </div>

                <form className={classNames(classes.bgLight, classes.ptMd4)}>
                    <div className={classNames(classes.container)}>

                        <Grid container direction="row" justify="center">

                            <Grid item xs={12} md={8} lg={6}>
                                <Grid container direction="row" justify="center" alignItems="start">
                                    <Grid item xs={12}>
                                        <TextField
                                            onChange={deliveryOrderChanged}
                                            fullWidth
                                            required
                                            value={deliveryData.nameSurname}
                                            field="nameSurname"
                                            errorText={this.state.errorTexts.nameSurname}
                                            onBlur={() => this.validateField("nameSurname")}
                                        />
                                    </Grid>

                                    <Grid item xs={12} className={classNames(classes.mt2)}>
                                        <TextField
                                            onChange={deliveryOrderChanged}
                                            fullWidth
                                            required
                                            value={deliveryData.email}
                                            field="email"
                                            errorText={this.state.errorTexts.email}
                                            onBlur={() => this.validateField("email")}
                                        />
                                    </Grid>

                                    <Grid item xs={12} className={classNames(classes.mt2)}>
                                        <SelectField
                                            onChange={this.handleChangeCountry}
                                            fullWidth
                                            required
                                            data={enums.data}
                                            value={deliveryData.country}
                                            field="country"
                                            textField="name"
                                            disabled={false}
                                            errorText={this.state.errorTexts.country}
                                            onBlur={() => this.validateField("name")}
                                        />
                                    </Grid>

                                    <Grid
                                        item
                                        xs={8}
                                        className={classNames(classes.mt2, classes.pr2)}
                                    >
                                        <TextField
                                            onChange={deliveryOrderChanged}
                                            fullWidth
                                            required
                                            value={deliveryData.city}
                                            field="city"
                                            errorText={this.state.errorTexts.city}
                                            onBlur={() => this.validateField("city")}
                                        />
                                    </Grid>

                                    <Grid item xs={4} className={classNames(classes.mt2)}>
                                        <TextField
                                            onChange={deliveryOrderChanged}
                                            fullWidth
                                            required
                                            value={deliveryData.psc}
                                            field="psc"
                                            errorText={this.state.errorTexts.psc}
                                            onBlur={() => this.validateField("psc")}
                                        />
                                    </Grid>

                                    <Grid
                                        item
                                        xs={8}
                                        className={classNames(classes.mt2, classes.pr2)}
                                    >
                                        <TextField
                                            onChange={deliveryOrderChanged}
                                            fullWidth
                                            required
                                            value={deliveryData.street}
                                            field="street"
                                            errorText={this.state.errorTexts.street}
                                            onBlur={() => this.validateField("street")}
                                        />
                                    </Grid>

                                    <Grid item xs={4} className={classNames(classes.mt2)}>
                                        <TextField
                                            onChange={deliveryOrderChanged}
                                            fullWidth
                                            required
                                            value={deliveryData.streetNumber}
                                            field="streetNumber"
                                            errorText={this.state.errorTexts.streetNumber}
                                            onBlur={() => this.validateField("streetNumber")}
                                        />
                                    </Grid>

                                    <Grid item xs={12} className={classNames(classes.mt2)}>
                                        <TelField
                                            onChange={deliveryOrderChanged}
                                            fullWidth
                                            required
                                            value={deliveryData.tel}
                                            field="tel"
                                            errorText={this.state.errorTexts.tel}
                                            onBlur={() => this.validateField("tel")}
                                        />
                                    </Grid>

                                    <Grid item xs={12} className={classNames(classes.mt7)}>
                                        <Checkbox
                                            onChange={deliveryOrderChanged}
                                            fullWidth
                                            required
                                            value={deliveryData.invoiceData}
                                            field="invoiceData"
                                        />
                                    </Grid>

                                    {deliveryData.invoiceData && (
                                        <>
                                            <Grid item xs={12} className={classNames(classes.mt4)}>
                                                <TextField
                                                    onChange={deliveryOrderChanged}
                                                    fullWidth
                                                    required
                                                    value={deliveryData.invoiceNameSurname}
                                                    field="invoiceNameSurname"
                                                    label={this.props.translate("field.nameSurname")}
                                                    errorText={this.state.errorTexts.invoiceNameSurname}
                                                    onBlur={() => this.validateField("invoiceNameSurname")}
                                                />
                                            </Grid>

                                            <Grid item xs={12} className={classNames(classes.mt2)}>
                                                <SelectField
                                                    onChange={deliveryOrderChanged}
                                                    fullWidth
                                                    required
                                                    data={enums.data}
                                                    value={deliveryData.invoiceCountry}
                                                    field="invoiceCountry"
                                                    textField="name"
                                                    label={this.props.translate("field.country")}
                                                    errorText={this.state.errorTexts.invoiceCountry}
                                                    onBlur={() => this.validateField("invoiceCountry")}
                                                />
                                            </Grid>

                                            <Grid
                                                item
                                                xs={8}
                                                className={classNames(classes.mt2, classes.pr2)}
                                            >
                                                <TextField
                                                    onChange={deliveryOrderChanged}
                                                    fullWidth
                                                    required
                                                    value={deliveryData.invoiceCity}
                                                    field="invoiceCity"
                                                    label={this.props.translate("field.city")}
                                                    errorText={this.state.errorTexts.invoiceCity}
                                                    onBlur={() => this.validateField("invoiceCity")}
                                                />
                                            </Grid>

                                            <Grid item xs={4} className={classNames(classes.mt2)}>
                                                <TextField
                                                    onChange={deliveryOrderChanged}
                                                    fullWidth
                                                    required
                                                    value={deliveryData.invoicePsc}
                                                    field="invoicePsc"
                                                    label={this.props.translate("field.psc")}
                                                    errorText={this.state.errorTexts.invoicePsc}
                                                    onBlur={() => this.validateField("invoicePsc")}
                                                />
                                            </Grid>

                                            <Grid
                                                item
                                                xs={8}
                                                className={classNames(classes.mt2, classes.pr2)}
                                            >
                                                <TextField
                                                    onChange={deliveryOrderChanged}
                                                    fullWidth
                                                    required
                                                    value={deliveryData.invoiceStreet}
                                                    field="invoiceStreet"
                                                    label={this.props.translate("field.street")}
                                                    errorText={this.state.errorTexts.invoiceStreet}
                                                    onBlur={() => this.validateField("invoiceStreet")}
                                                />
                                            </Grid>

                                            <Grid item xs={4} className={classNames(classes.mt2)}>
                                                <TextField
                                                    onChange={deliveryOrderChanged}
                                                    fullWidth
                                                    required
                                                    value={deliveryData.invoiceStreetNumber}
                                                    field="invoiceStreetNumber"
                                                    label={this.props.translate("field.streetNumber")}
                                                    errorText={this.state.errorTexts.invoiceStreetNumber}
                                                    onBlur={() => this.validateField("invoiceStreetNumber")}
                                                />
                                            </Grid>
                                        </>
                                    )}

                                    <Grid item xs={12} className={classNames(classes.mt7)}>
                                        <Checkbox
                                            onChange={deliveryOrderChanged}
                                            fullWidth
                                            required
                                            value={deliveryData.companyOrder}
                                            field="companyOrder"
                                        />
                                    </Grid>

                                    {deliveryData.companyOrder && (
                                        <>
                                            <Grid item xs={12} className={classNames(classes.mt4)}>
                                                <TextField
                                                    onChange={deliveryOrderChanged}
                                                    fullWidth
                                                    required
                                                    value={deliveryData.ico}
                                                    field="ico"
                                                    errorText={this.state.errorTexts.ico}
                                                    onBlur={() => this.validateField("ico")}
                                                />
                                            </Grid>

                                            <Grid item xs={12} className={classNames(classes.mt2)}>
                                                <TextField
                                                    onChange={deliveryOrderChanged}
                                                    fullWidth
                                                    required
                                                    value={deliveryData.dic}
                                                    field="dic"
                                                    errorText={this.state.errorTexts.dic}
                                                    onBlur={() => this.validateField("dic")}
                                                />
                                            </Grid>
                                            {config.locale !== 'pl-PL' && <Grid item xs={12} className={classNames(classes.mt2)}>
                                                <TextField
                                                    onChange={deliveryOrderChanged}
                                                    fullWidth
                                                    required
                                                    value={deliveryData.icdph}
                                                    field="icdph"
                                                    errorText={this.state.errorTexts.icdph}
                                                    onBlur={() => this.validateField("icdph")}
                                                />
                                            </Grid>}

                                            <Grid item xs={12} className={classNames(classes.mt2)}>
                                                <TextField
                                                    onChange={deliveryOrderChanged}
                                                    fullWidth
                                                    required
                                                    value={deliveryData.bic}
                                                    field="bic"
                                                    errorText={this.state.errorTexts.bic}
                                                    onBlur={() => this.validateField("bic")}
                                                />
                                            </Grid>

                                            <Grid item xs={12} className={classNames(classes.mt2)}>
                                                <TextField
                                                    onChange={deliveryOrderChanged}
                                                    fullWidth
                                                    required
                                                    value={deliveryData.iban}
                                                    field="iban"
                                                    errorText={this.state.errorTexts.iban}
                                                    onBlur={() => this.validateField("iban")}
                                                />
                                            </Grid>

                                            <Grid item xs={12} className={classNames(classes.mt2)}>
                                                <TextField
                                                    onChange={deliveryOrderChanged}
                                                    fullWidth
                                                    required
                                                    value={deliveryData.accountOwner}
                                                    field="accountOwner"
                                                    errorText={this.state.errorTexts.accountOwner}
                                                    onBlur={() => this.validateField("accountOwner")}
                                                />
                                            </Grid>
                                        </>
                                    )}

                                    <>
                                        <Grid item xs={12}>
                                            <h4
                                                className={classNames(
                                                    classes.mt8,
                                                    classes.mb4,
                                                    classes.uppercase
                                                )}
                                            >
                                                {this.props.translate("delivery")}
                                            </h4>
                                        </Grid>

                                        <Grid item xs={12}>
                                            <div className={classNames(classes.flexCol, classes.w100)}>
                                                {transports &&
                                                    transports.data.map((item) => {
                                                        if (this.state.onlyChallenge && item.id === 8)
                                                            return null;

                                                        return (
                                                            <>
                                                                <div
                                                                    className={classNames(
                                                                        classes.dFlex,
                                                                        classes.w100,
                                                                        classes.alignItemsCenter,
                                                                        classes.justifyContentBetween,
                                                                        classes.py3
                                                                    )}
                                                                    onClick={(e) =>
                                                                        deliveryOrderChanged("deliveryType", item._id)
                                                                    }
                                                                >
                                                                    <div
                                                                        className={classNames(
                                                                            classes.dFlex,
                                                                            classes.alignItemsCenter,
                                                                            classes.justifyContentBetween
                                                                        )}
                                                                    >
                                                                        {/* <div>
                                                                        ikona
                                                                    </div> */}
                                                                        <div
                                                                            className={classNames(
                                                                                classes.textM,
                                                                                classes.textMono
                                                                            )}
                                                                        >
                                                                            {item.name}
                                                                        </div>
                                                                    </div>

                                                                    <div
                                                                        className={classNames(
                                                                            classes.dFlex,
                                                                            classes.alignItemsCenter,
                                                                            classes.justifyContentBetween
                                                                        )}
                                                                    >
                                                                        <div
                                                                            className={classNames(
                                                                                classes.mr3,
                                                                                classes.fontBold
                                                                            )}
                                                                        >
                                                                            {formatPrice(item.price)}
                                                                        </div>
                                                                        <div>
                                                                            <Radio
                                                                                onChange={(e) =>
                                                                                    deliveryOrderChanged(
                                                                                        "deliveryType",
                                                                                        item._id
                                                                                    )
                                                                                }
                                                                                value={item._id}
                                                                                checked={
                                                                                    deliveryData.deliveryType === item._id
                                                                                }
                                                                                classes={{ root: classes.p0 }}
                                                                            />
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                                <hr className={classNames(classes.hrLine)} />
                                                            </>
                                                        );
                                                    })}
                                            </div>
                                        </Grid>
                                    </>

                                    <Grid item xs={12}>
                                        <h4
                                            className={classNames(
                                                classes.mt8,
                                                classes.mb4,
                                                classes.uppercase
                                            )}
                                        >
                                            {this.props.translate("payment")}
                                        </h4>
                                    </Grid>

                                    <Grid item xs={12}>
                                        <div className={classNames(classes.flexCol, classes.w100)}>
                                            {deliveryData.paymentMethods.map((item) => {
                                                if (this.state.onlyChallenge && item.id === 8) return null;

                                                return (
                                                    <>
                                                        <div
                                                            className={classNames(
                                                                classes.dFlex,
                                                                classes.w100,
                                                                classes.alignItemsCenter,
                                                                classes.justifyContentBetween,
                                                                classes.py3
                                                            )}
                                                            onClick={(e) =>
                                                                deliveryOrderChanged("paymentMethod", item.id)
                                                            }
                                                        >
                                                            <div
                                                                className={classNames(
                                                                    classes.dFlex,
                                                                    classes.alignItemsCenter,
                                                                    classes.justifyContentBetween
                                                                )}
                                                            >
                                                                <div
                                                                    className={classNames(
                                                                        classes.paymentIconHolder,
                                                                        classes.dFlex,
                                                                        classes.w100,
                                                                        classes.justifyContentCenter,
                                                                        classes.alignItemsCenter
                                                                    )}
                                                                >
                                                                    {item.id === 5 ? (
                                                                        <CreditCard
                                                                            size={25}
                                                                            className={classes.textGray500}
                                                                        />
                                                                    ) : (
                                                                        <Exchange
                                                                            size={25}
                                                                            className={classes.textGray500}
                                                                        />
                                                                    )}
                                                                </div>
                                                                <div
                                                                    className={classNames(
                                                                        classes.ml5,
                                                                        classes.textM,
                                                                        classes.textMono
                                                                    )}
                                                                >
                                                                    {this.props.translate(item.name)}
                                                                </div>
                                                            </div>

                                                            <div
                                                                className={classNames(
                                                                    classes.dFlex,
                                                                    classes.alignItemsCenter,
                                                                    classes.justifyContentBetween
                                                                )}
                                                            >
                                                                <div
                                                                    className={classNames(
                                                                        classes.mr3,
                                                                        classes.fontBold
                                                                    )}
                                                                >
                                                                    {formatPrice(item.price)}
                                                                </div>
                                                                <div>
                                                                    <Radio
                                                                        onChange={(e) =>
                                                                            deliveryOrderChanged("paymentMethod", item.id)
                                                                        }
                                                                        value={item.id}
                                                                        checked={deliveryData.paymentMethod === item.id}
                                                                        classes={{ root: classes.p0 }}
                                                                    />
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <hr className={classNames(classes.hrLine)} />
                                                    </>
                                                );
                                            })}
                                        </div>
                                    </Grid>

                                    <Grid item xs={12} className={classNames(classes.mt7)}>
                                        <div className={classNames(classes.dFlex, classes.justifyContentCenter)}>
                                            <Button
                                                onClick={this.goToSummary}
                                                className={classNames(classes.mb5, classes.mt4)}
                                                disabled={!this.state.isValid}
                                                fullwidth
                                                arrow
                                            >
                                                {this.props.translate("continue")}
                                            </Button>
                                        </div>
                                    </Grid>
                                </Grid>

                            </Grid>
                        </Grid>
                    </div>
                </form>
            </>
        );
    }
}

export default compose(
    withStyles(styles),
    withLocalization,
    withStore,
    withDoby,
    withUser
)(DeliveryPayment);
